.list {
    display: flex;
    flex-direction: column;
    background-color: rgb(241, 170, 232);
    display: inline-block;
    width: 100%;
    margin: auto;
}

.navbar {
    display: flex;

    display: inline-block;
}

.navbar li {

    list-style: none;
    display: inline-block;
    margin: auto;
    text-decoration: none;
    color: rgb(29, 25, 231);
    font-size: 18px;
    padding: 8px 20px;
}

.navbar li:hover {
    color: rgb(151, 7, 247);
    border: darkmagenta;
    border-radius: 2px;
    font-size: 20px;
}

a {
    color: black;

}